












import Vue from 'vue'

export default Vue.extend({
  name: 'DropFile',
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    dragging: false,
  }),
  methods: {
    dblclick() {
      this.$emit('doubleClick')
    },
    dragEnter() {
      if (!this.disabled) {
        this.dragging = true
        this.$emit('dragEnter')
      }
    },
    dragLeave() {
      if (!this.disabled) {
        this.dragging = false
        this.$emit('dragLeave')
      }
    },
    drop(event: DragEvent) {
      if (!this.disabled) {
        this.dragging = false
        this.$emit('drop', event)
      }
    },
  },
})
