
















import Vue from 'vue'

export default Vue.extend({
  name: 'LoadFile',
  props: {
    accept: {
      type: String,
      default: '*',
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    click() {
      if (!this.disabled) (this.$refs.fileReader as HTMLElement).click()
    },
    change(event: Event) {
      this.$emit('change', event)
    },
  },
})
